<template>
  <Pokemon v-if="pokemon" :pokemon='pokemon' />
</template>

<script>
import components from "./script/components";
import computed from "./script/computed";
import methods from "./script/methods";
import watch from "./script/watchers";
import axios from "axios";

export default {
  data() {
    return {
      pokemon: false
    };
  },
  components,
  computed,
  methods,
  watch,
  created() {
    const tier = this.$route.params.tier;
    const pkmn = this.$route.params.pokemon;
    axios.get(`/pokemon/${tier}/${pkmn}`)
      .then(res => {
        this.pokemon = res.data.pokemon
      })
  }
};
</script>

<style lang="scss">
@import "./style";
</style>